import { DefaultLayout } from "../../layouts";
import { FooterOrganism, HeaderOrganism, PijnOrganism } from "../../organisms";

export default function PijnScreen() {
  return (
    <DefaultLayout
      header={<HeaderOrganism />}
      body={
        <>
          <PijnOrganism />
        </>
      }
      footer={<FooterOrganism />}
    />
  );
}
